html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
}

header {
  position: fixed;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  padding: 0.86vw 0 0.86vw 0;
  z-index: 9999;
  background-color: #fff;
}
header .content-wrap {
  position: relative;
  padding: 0 6vw 0 6vw;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
}
header .content-wrap ul {
  display: flex;
  align-items: center;
}
header .content-wrap li {
  margin-right: 2.34vw;
  line-height: 1.25rem;
}
header .content-wrap li:last-child {
  margin-right: 0px !important;
}
header .content-wrap li p {
  font-size: 1.1em;
  font-weight: 300;
  color: #222226;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
header .content-wrap .header-logo {
  height: auto;
  max-width: 70px;
  place-self: center;
}
header .content-wrap .search {
  justify-self: end;
}
header .content-wrap svg {
  width: 55px;
  height: 25px;
}